/**
 * Preprocess TeX code to convert things that KaTeX doesn't know how to handle
 * to things is does.
 */
module.exports = function(texCode) {
    return texCode.replace(/\{align[*]?\}/g, "{aligned}").replace(/[\u00a0]/g, " ");
};


